.popup {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.7);
	visibility: hidden;
	opacity: 0;
	overflow-y: auto;
	overflow-x: hidden;
	z-index: 50;
	transition: all 1s 0s;
	&.popup_show{
		opacity: 1;
		visibility: visible;
		& .popup__content{
			opacity: 1;
			transform: rotate(0deg) scale(1);
		}
	}
	&__body {
		min-height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 30px 10px;
	}
	&__content {
		background: #999;
		max-width: 800px;
		padding: 30px 0px 0px 0px;
		position: relative;
		opacity: 0;
		perspective: 600px;
		perspective-origin: center;
		transform: rotate(45deg) scale(1);
		transition: all 1s 0s;
		display:grid;
		grid-template: auto 1fr auto /1fr;
	}
	&__close {
		position: absolute;
		top: 0;
		right: 10px;
		width: 30px;
		height: 30px;
		transition: all 0.3s 0s;
		transform: scale(1);
		&:before,&:after{
			content: '';
			position: absolute;
			top: 15px;
			left: 0;
			width: 30px;
			height: 2px;
			background-color: #333;
			transition: all 0.3s 0s;
		}
		&:before{
			transform: rotate(45deg) scale(1);
		}
		&:after{
			transform: rotate(-45deg) scale(1);
		}
	}
}

.button__popup{
	justify-self: end;
	margin: 0px 0px 0px 5px;
}

